body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.rotating-box {
    margin: 0 auto;
    height: 20rem;
    width: 28rem;
    -webkit-perspective: 170rem;
            perspective: 170rem;
}

.rotating-box__rb { 
    -webkit-transform-style: preserve-3d; 
            transform-style: preserve-3d;
}

.rotating-box__rb img {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
    width: 23rem;
    height: 15rem;
    border-radius: 5px;
    cursor: pointer;
}

.rotating-box__front {
    -webkit-transform: translateZ(14rem);
            transform: translateZ(14rem);
    height: 20rem;
    width: 28rem;
    position: absolute;
}

.rotating-box__back {
    -webkit-transform: rotateY(180deg) translateZ(14rem);
            transform: rotateY(180deg) translateZ(14rem);
    height: 20rem;
    width: 28rem;
    position: absolute;
}

.rotating-box__left {
    -webkit-transform: rotateY(-90deg) translateX(-14rem);
            transform: rotateY(-90deg) translateX(-14rem);
    -webkit-transform-origin: left;
            transform-origin: left;
    height: 20rem;
    width: 28rem;
    position: absolute;
}

.rotating-box__right {
    -webkit-transform: rotateY(90deg) translateX(14rem);
            transform: rotateY(90deg) translateX(14rem);
    -webkit-transform-origin: right;
            transform-origin: right;
    height: 20rem;
    width: 28rem;
    position: absolute;
}

.img-closer {
    position: absolute;
    top: 0;
    right: 0;
    height: 1.3rem;
    width: 1.3rem;
    border: .2rem rgb(94, 88, 88) solid;
    border-radius: 50%;
    color: rgb(94, 88, 88);
    font-weight: bold;
    text-align: center;
    cursor: pointer;
}
.footer-flex {
    display: flex;
    align-content: center;
    justify-content: center
}
